* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: "Heebo", sans-serif;
}

.nav-list {
  height: 50px;
  display: flex;
  width: 20%;
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
  list-style-type: none;
}

.nav-links {
  text-decoration: none;
  color: #000000;
  font-size: 20px;
  font-family: "Heebo", sans-serif;
}

.home,
.about {
  height: 100vh;
  position: absolute;
  width: 100%;
}

.home {
  background-image: linear-gradient(to top, #a8edea 0%, #fed6e3 100%);
}

.about {
  background-image: linear-gradient(120deg, #89f7fe 0%, #66a6ff 100%);
}

.home-content,
.about-content {
  height: 80%;
  width: 100%;
  margin-top: 100px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  img {
    height: 80%;
    margin: 0px;
  }
  h1 {
    font-size: 50px;
    margin: 20px 0px;
  }
  p {
    font-size: 20px;
    margin: 10px 0px;
  }
}
